@import "../Common/Styles/SASS/Variables";


.burgerNav {
  //width: 100%;
  height: 0;
  //display: flex;
  position: fixed;
  //inset: 0;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;


  @media (min-width: 801px) {
    display: none;
  }

  .burgerNavItems {
    //display: flex;
    //flex-direction: column;
    //justify-content: space-between;
    //align-items: center;
    //flex-wrap: nowrap;
    //background-color: $transparent_Background_Color;
    //scroll-behavior: smooth;
    //position: absolute;
    //top: -180px;
    //right: 320px;
    //left: 0;
    //height: 0;
    //padding: 20px;
    //transform: translate(0, -150);
    //transition: 0.7s;


    display: flex;
    flex-basis: 100%;
    flex-grow: 1;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(30, 30, 30, 0.95);
    /* scroll-behavior: smooth; */
    /* position: absolute; */
    /* top: -180px; */
    /* right: 320px; */
    /* left: 0; */
    ///* height: 0; */
    /* padding: 20px; */
    transform: translate(0, -150);
    transition: 0.7s;
    margin: -120px;


    //display: flex;
    //  flex-basis: 100%;
    //  flex-grow: 1;
    //  align-items: center;
    //flex-direction: column;
    //justify-content: space-between;
    //background-color: rgba(30, 30, 30, 0.95);
    //scroll-behavior: smooth;
    //position: absolute;
    //top: -180px;
    //transform: translate(0, -150);
    //transition: 0.7s;

    &.burgerNav :hover, .active {
      color: $textColor_Primary;
      background-color: transparent;
    }

    a {
      color: $textColor_h3;
    }

  }

  .burgerBtn {
    display: inline-block;
    justify-content: end;
    width: 1.5em;
    height: 1.5em;
    vertical-align: middle;
    background: no-repeat center center;
    background-size: 100% 100%;
    font-size: 1.25rem;
    line-height: 1;
    position: absolute;
    top: 14px;
    //left: 300px;
    //right: 40%;
    border: 1px solid $textColor_Span;
    cursor: pointer;
    border-radius: 5px;

    @media (max-width: 801px) {
      right: 80px;
    }
    @media (max-width: 540px) {
      right: 50px;
    }
    @media (max-width: 420px) {
      right: 40px;
    }
    @media (max-width: 280px) {
      right: 24px;
    }


    .spanBurgerBtn,
    .spanBurgerBtn::before,
    .spanBurgerBtn::after {
      position: absolute;
      top: 50%;
      margin-top: -1px;
      left: 50%;
      margin-left: -10px;
      width: 20px;
      height: 2px;
      background-color: $textColor_Span;
    }

    .spanBurgerBtn::before,
    .spanBurgerBtn::after {
      content: "";
      display: block;
      transition: 0.5s;
    }

    .spanBurgerBtn::before {
      transform: translateY(-5px);
    }

    .spanBurgerBtn::after {
      transform: translateY(5px);
    }

    .spanBtn_active {
      height: 0;
    }

    .spanBtn_active::before {
      transform: rotate(45deg);
    }

    .spanBtn_active::after {
      transform: rotate(-45deg);
    }
  }

  .show {
    //display: flex;
    //flex-direction: column;
    //justify-content: space-between;
    //align-items: center;
    //flex-wrap: nowrap;

    //top: 8px;
    //inset: 0;
    //transform: translate(0, 0);
    height: 150px;
    position: fixed;
    top: 120px;
    right: 0;
    bottom: 0;
    left: 0;
    //transition: 0.7s;
    animation: menuFromTop 0.7s;
  }
}

@keyframes menuFromTop {
  from {
    transform: translateY(-150px);
  }
  to {
    transform: translateY(0);
  }
}

@-webkit-keyframes menuFromTop {
  from {
    transform: translateY(-150px);
  }
  to {
    transform: translateY(0);
  }
}